html {
    font: Roboto, sans-serif;
}
.current {
    color: white;
    background-color: navy;
}

nav {
    display: flex;
    flex-direction: row;
}

nav a {
    display: block;
    padding: 0.5em;
    background-color: lightblue;
    text-transform: capitalize;
    text-decoration: none;
    min-width: 80px;
    text-align: center;
    margin-right: 10px;
    color: black;
}